import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/Home'
Vue.use(Router)
export  default  new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      component: Home
    },
    {
      path: '/navbar',
      component: () => import('./views/Navbar.vue')
    },
    {
      path: '/form',
      component: () => import('./views/Form.vue')
    },
    {
      path: '/footer',
      component: () => import('./views/Footer.vue')
    },
    {
      path: '/form/form1',
      component: () => import('@/components/FormExample/Form1.vue')
    },
    {
      path: '/navbar/navbar1',
      component: () => import('@/components/NavBarExample/NavBar1.vue')
    },
    {
      path: '/footer/footer1',
      component: () => import('@/components/FooterExample/Footer1.vue')
    }
  ]
})